.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
/* 사용자 */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,ol,li{
  list-style: none;
}
a{
  text-decoration: none;
  color: #333;
}
img{
  max-width: 100%;
  vertical-align: top;
}
#header{
  position: relative;
  width: 100%;
  height: 122px;
  z-index: 10;
  /* background: #f90; */
  background: transparent;
}
.up{
  width: 1100px;
  height: 78px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.left{
  width: 200px;
  height: auto;
  position: relative;  
}
.language{
  position: absolute;
  width: 200px;
  height: 35px;
  line-height: 35px;
  left: 0; 
  top: 10px;
  text-align: left;
}
.language>a{
  position: absolute;
  width: 200px;
  height: 22px;
  line-height: 22px;
  left: 0;
  top: 6px;
  color: #fff;
}
.language>a>span{
  position: absolute;
  display: inline-block;
  width: 40px;
  height: 100%;
  right: 30px; 
  top: 5px;
  background: url(/public/images/h_0.png) no-repeat 0px center;
}
.language span>img{
  opacity: 0;
  margin-top: 6px;
}
#header:hover .language a{
  color: #333;
}
#header:hover .language span>img{
  opacity: 1;
}
#header:hover .language span{
  background: none;
}
.left ul{
  position: absolute;
  width: 200px;
  background: #fff;
  left: 0;
  top: 60px;
  padding: 10px 0;
  text-align: left;
  border: 1px solid #fff;
  z-index: 11;
  font-size: 14px;
}
.left a{
  display: block;
  padding: 0 10px;
  line-height: 30px;
}
.left .list a:hover{
  background: #ccc;
}
#logo{
  width: 222px;
  height: 42px;
  margin: 18px auto;
  text-align: center;
}
#logo a{
  display: block;
  width: 222px;
  height: 42px;
  background: url(/public/images/logo_1.png) no-repeat left top/cover;
}
#header:hover #logo a{
  background-image: url(/public/images/logo_0.png);
}
.util ul{
  display: flex;
  margin-top: 20px;
  font-size: 14px;
}
.util ul li{
  padding: 0 10px;
  border-right: 1px solid #ccc;
}
.util ul li a{
  color: #fff;
}
.util ul li:last-child{
  border-right: none;
}
.util ul li:last-child a{
  display: block;
  width: 33px; height: 18px;
  background: url(/public/images/ser_0.png) no-repeat center/auto;
}
#header:hover a{
  color: #666;
}
#header:hover{
  background: #fff;
}
#header:hover .util li:last-child a{
  background-image: url(/public/images/ser_1.png);
}
.down{
  height: 44px;
  text-align: center;
  position: relative;
}
#nav>ul{
  display: flex;
  align-items: center;
  height: 44px;
  justify-content: center;
}
#nav>ul>li{
  padding: 0 20px;
}
#nav>ul>li>a{
  position: relative;
  color: #fff;
}
#nav>ul>li>a:after{
  content: "";
  display: block;
  position: absolute;
  left: 0%;
  bottom: -11px;
  height: 3px;
  width: 0%;
  background: #f90;
  transition: all .5s;
  z-index: 101;
}
#nav>ul>li>a:hover:after{
  width: 100%;
}
#nav .sub_back{
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  top:44px;
  text-align: right;
  z-index: 100;
  padding-bottom: 10px;
  display: none;
}
#nav .sub_back .sub_in{
  width: 1100px;
  margin: 0 auto;
}
#nav .sub_back .sub_in>ul{
  width: 500px;
  float: right;
  text-align: left;
}
#nav .sub_back .sub_in>ul>li{
  display: inline-block;
}
#nav .sub_back .sub_in>ul>li>a{
  display: block;
  padding: 10px 20px;
  color: #666;
  margin: 10px 0;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul{
  width: 100%;
  text-align: center;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul>li{
  margin: 0 80px;
  vertical-align: top;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul>li>a{
  color: #333;
  font-weight: 700;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul ol{
  text-align: left;
  margin-left: 25px;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul>li:nth-child(2) ol{
  margin-left: 40px;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul>li:nth-child(3) ol{
  margin-left: 20px;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul ol li{
  line-height: 40px;
}
#nav>ul>li:hover .sub_back{
  display: block;
}
#nav>ul>li:nth-child(1) .sub_back,
#nav>ul>li:nth-child(2) .sub_back,
#nav>ul>li:nth-child(3) .sub_back,
#nav>ul>li:nth-child(4) .sub_back,
#nav>ul>li:nth-child(5) .sub_back{
  display: none;
}
#nav>ul>li:nth-child(6) .sub_back .sub_in>ul{
  margin-right: 70px;
}
#nav>ul>li:nth-child(7) .sub_back .sub_in>ul{
  margin-right: 88px;
}
#nav>ul>li:nth-child(8) .sub_back .sub_in>ul{
  margin-right: -20px;
}
#main{
  margin-top: -122px;
  height: 670px;
  overflow: hidden;
  position: relative;
}
#main .txtBox{
  position: absolute;
  width: 1440px;
