  height: 55px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fmenu{
  width: 820px;
  height: 55px;
}
.fmenu ul{
  width: 820px;
  height: 55px;
  display: flex;
  align-items: center;
}
.fmenu ul a{
  padding: 3px 14px 3px 0;
  display: block;
  margin-left: 14px;
  border-right: 1px solid #767676;
}
.fmenu ul li:nth-child(4) a{
