  height: 55px;
}
.fsns ul{
  width: 240px;
  height: 55px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fsns li{
  width: 30px;
  height: 30px;
}
.fsns li:first-child{
  width: 42px;
}
.fdown{
  height: 63px;
  border-top: 1px solid #ccc;
  padding-top: 15px;
}
.fdown .footIn{
  display: block;
  text-align: left;
}
.fdown .tel{
  height: 40px;
  font-size: 13px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
